.loot-box {
    position: absolute;
    bottom: 0px;
    left: 0px;
    right: 0px;
    margin-left: auto;
    margin-right: auto;
    width: 200px;
    z-index: 1000000000;
    //background-color: red;
    display: flex;
    padding: 20px;
    backdrop-filter: blur(8px);
    background-color: #0000003b;

    .loot-option {
        margin: 3px;
        img {
            width: 30px;
            height: 30px;
        }
    }
}
